import React from 'react';
import { connect } from 'react-redux';
import ReactModal from 'react-modal';
import AvaiModal from '../Modals/avaiModal';
import styled from 'styled-components';

const mapStateToProps = (state) => ({
    ...state.modal
})

const customStyles = {
    content : {
      top                   : '0',
      left                  : '0',
      width                 : '100%',
      height                :  '100%',
      position              : 'fixed'
    }
  };
  

class ModalContainer extends React.Component {
    state = {
        modalIsOpen: false
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps !== this.props) {
            this.setState({
                modalIsOpen: nextProps.modalProps.on
            })
        }
    }

    closeModal = () => {
        this.setState({ modalIsOpen: false })
    }

    render() {
        if (!this.props.modalType) {
            return null;
        }
         
        const view = this.props.modalType === "availability" ? (
            <AvaiModal closeModal={this.closeModal} {...this.props.modalProps} />
        ) : null;

        return (
            <React.Fragment>
                <ReactModal
                    isOpen={this.state.modalIsOpen}
                    onAfterOpen={this.afterOpenModal}
                    onRequestClose={this.closeModal}
                    overlayClassName="modal fade show"
                    bodyOpenClassName="modal-open"
                    className="modal-dialog modal-dialog-centered"
                    contentLabel=""
                    ariaHideApp={false}
                    style={customStyles}
                >
                    <StyledModal className={this.props.backdrop ? 'fade':''}>{view}</StyledModal>
                </ReactModal>
            </React.Fragment>
        )
    }
};

export default connect(mapStateToProps, null)(ModalContainer)